<template>

  <v-container>
    <v-layout column wrap align-center justify-center >
      <v-flex>
      </v-flex>
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-12">
          <v-toolbar dark color="teal darken-2">
            <v-toolbar-title>สมัครสมาชิกส่งยอดแบบเป็นทีม</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-flex>{{userId}} -- {{profile}}</v-flex>
            <v-form ref="regForm" v-model="valid" lazy-validation>
              <v-text-field disabled v-if="seen" id="hide"
                prepend-icon="G"
                v-model="groupId"
              ></v-text-field>
              <v-text-field disabled v-if="seen" id="useridprofilefield"
                v-model="userId"
              ></v-text-field>
              <v-text-field disabled v-if="seen" id="groupidfield"
                v-model="profile"
              ></v-text-field>

              <v-text-field
                v-model="groupName" 
                prepend-icon="account_circle"
                :rules="[v => !!v || 'กรุณากรอกชื่อทีมของท่าน']"
                name="results"
                label="ชื่อกลุ่ม"
              ></v-text-field>
              <v-text-field
                v-model.number="groupMember"
                prepend-icon="group"
                :rules="[v => !!v || 'กรุณากรอกจำนวนสมาชิกทีมของท่าน']"
                name="results"
                label="จำนวนสมาชิก"
                type="number"
              ></v-text-field>
              <v-text-field
                v-model="coordinator"
                prepend-icon="face"
                name="results"
                label="ผู้ประสานงาน"
              ></v-text-field>
              <v-text-field
                v-model="coordinatorTel"
                prepend-icon="call"
                name="results"
                label="เบอร์โทรผู้ประสานงาน"
                type="social"
              ></v-text-field>
              <v-text-field
                v-model="groupIdOld"
                prepend-icon="G"
                name="results"
                label="รหัสกลุ่มเดิม (ถ้ามี)"
              ></v-text-field>              
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn flat color="red lighten-1" @click="clear()">ล้างฟอร์ม</v-btn>
            <v-spacer />
            <router-link :to="{name: 'view-noid', params: {groupName: this.groupName}}">
              <v-btn :disabled="!valid" color="green darken-4" round dark large
                @click="saveRegisterGroup()"
                >สมัคร
              </v-btn>
            </router-link>
          </v-card-actions>
        </v-card>
      </v-flex>

      <!-- <v-flex xs12 sm8 md6>
        <ConfirmRegis
          :confirmed-regis="confirmedRegis"
          :result="bundleForm"
          :name-result="groupName"
          :member-result="groupMember"
          @emitCloseRegis="confirmedRegis = arguments[0]"
        />
      </v-flex> -->

      
    </v-layout>
  </v-container>


  <!--<v-card>
    <v-toolbar dark color="teal darken-2">
      <v-toolbar-title>ตรวจสอบข้อมูล</v-toolbar-title>
    </v-toolbar>

    <v-card-text>
      <v-layout column justify-center align-center>
        <v-flex xs12 class="teal--text">
          <p class="title font-weight-bold">ชื่อกลุ่ม</p>
          <p class="display-4 font-weight-bold light-teal--text">{{ groupName }}</p>
        </v-flex>
        <v-flex xs12><p>&nbsp;</p></v-flex>
        <v-flex xs12 class="grey--text">
          <p class="title font-weight-bold">จำนวนสมาชิก</p>
          <p class="display-4 font-weight-bold ">
            {{ groupMember }}
          </p>
        </v-flex>
      </v-layout>
    </v-card-text>

    <v-divider></v-divider>

    <v-card-actions>
      <v-btn
        color="primary"
        flat
        @click="dialog = false"
      >
        กลับไปแก้ไข
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn color="teal darken-2" round dark large @click="saveRegisterGroup()"
      >
        ยืนยัน
      </v-btn>

    </v-card-actions>
  </v-card>-->
    
</template>

<script>
  import firebaseApp from "@/components/firebaseInit";
  import * as firebase from 'firebase';
  import { mapGetters } from "vuex";

  export default {
    name: "register-group",
    // components: {
    //   ConfirmRegis
    // },
    data: () => ({
      confirmedRegis: "",
      valid: false,
      idGroup: '',
      groupId: '',
      groupIdOld: '',
      groupName: '',
      groupMember: '',
      coordinator: '',
      coordinatorTel: '',
      create: null,
      groups: [],
      userId: null,
      groupIdLine: null,
      displayName: null,
      bundleForm: {},
      profile: '',
      seen: ''
    }),
    created() {
      firebaseApp.collection("yodByUser")
      //.doc('3WLksiIH82EFlzTMzKDw').collection('transactions')
        //.orderBy('groupId')
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            // console.log(doc.data());
            const data = {
              'id': doc.id,
            };
            this.groups.push(data);
          });
            const idGroup = this.groups.length;
            console.log(idGroup);
            //this.groupId.push(idGroup);
        });
    },
    mounted() {
      this.$liff.init(
        data => {
          this.userId = data.context.userId;
          console.log(userId);
          this.getProfile();
        },
        error => {
          console.log(error);
        }
      );
    },
    methods: {
      clear() {
        this.$refs.regForm.reset();
        this.bundleForm = {};
      },
      confirmed() {
        this.$refs.regForm.validate();

        this.bundleForm = {
          groupId: this.groupId,
          groupName: this.groupName,
          groupMember: this.groupMember,
          coordinator: this.coordinator,
          coordinatorTel: this.coordinatorTel,
          groupIdOld: this.groupIdOld
        };
      },
      getProfile() {
        this.$liff
          .getProfile()
          .then(user => {
            this.profile = user;
            //this.setUserInfo(user);
          })
          .catch(error => alert(`Error getting Profile: ${error}`));
      },
      saveRegisterGroup () {
        //this.$refs.regForm.validate();
        firebaseApp.collection('yodByUser').doc(`G${this.groups.length}`).set({
          groupId: `G${this.groups.length}`,
          groupName: this.groupName,
          groupMember: this.groupMember,
          coordinator: this.coordinator,
          coordinatorTel: this.coordinatorTel,
          groupIdOld: this.groupIdOld,
          lineId: `${this.userId}`,
          lineProfile: `${this.profile}`,
          create: firebase.firestore.FieldValue.serverTimestamp()
        })
        //.then(docRef => this.$router.push('/group'))
        .catch(error => console.log(err))
      }
    }
  }
</script>

