import Vue from 'vue'
import Router from 'vue-router'
import RegisGroup from '@/views/RegisterGroup.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'register-group',
      component: RegisGroup
    },
    {
      path: "/group",
      name: "group-list",
      component: () => import("@/views/GroupList.vue")
    },
    {
      path: "/editgroup/:groupName",
      name: "edit-group",
      component: () => import("@/views/EditGroup.vue")
    },
    {
      path: "/view/:groupId",
      name: "view-group",
      component: () => import("@/views/ViewGroup.vue")
    },
    {
      path: "/viewnoid/:groupName",
      name: "view-noid",
      component: () => import("@/views/ViewGroupNoId.vue")
    },
    {
      path: "/viewyodgroup/:groupId",
      name: "view-yod-group",
      component: () => import("@/views/ViewYodGroup.vue")
    },
    {
      path: "/form",
      name: "form-submit",
      component: () => import("@/views/FormSubmit.vue")
    },
    {
      path: "/edit/:employeeId",
      name: "edit-employee",
      component: () => import("@/components/EditEmployee.vue")
    },
    {
      path: "/new",
      name: "new-employee",
      component: () => import("@/components/NewEmployee.vue")
    },
    {
      path: "/:employeeId",
      name: "view-employee",
      component: () => import("@/components/ViewEmployee.vue")
    }
  ]
})



